import * as React from "react";
import type { HeadFC, PageProps } from "gatsby";
//import Layout from "../components/Layout";
import Architect from "../components/architect";
import { OrbitCanvas } from "../common/three/orbit";
import { Box } from "../common/three/box";
import { TextMesh } from "../common/three/text";

const IndexPage: React.FC<PageProps> = () => {
  const userInfo = {
    name: 'Balaganesh Jagadeesan',
  };

  const [name, setName] = React.useState(userInfo.name);

  // React.useEffect(() => {
  //   setName
  // });

  return <Architect pageTitle={`Golden Ticket`} openSidebar={false} showAppHeader={false} showAppSidebar={false} showPageTitle={false}>
    <OrbitCanvas scene="gold4k">
      <Box position={[0, 0, 0]} onClick={() => setName('Karthick Shanmugasundaram')} />
      <Box position={[1.2, 0, 0]} onClick={() => setName('Flora Christopher')} />
      <Box position={[-1.2, 0, 0]} autoRotate={true} onClick={() => setName('Jared Barnes')} />

      <TextMesh text={'Awarding a Distinguished Golden Ticket to:'} size={1} position={[0, 3, -2]} color='gold' />
      <TextMesh text={name} size={2} position={[0, 1.2, -2]} texture={'liquidGold'} autoRotate={true} />
    </OrbitCanvas>
  </Architect>
}

export default IndexPage

export const Head: HeadFC = () => (
  <>
    <title>Welcome</title>
    <meta name="description" content="My team is an effective team." />
  </>
);
